<template>
  <div>
    <a-form :model="initParamsData" :label-col="labelCol" :wrapper-col="wrapperCol">
      <a-form-item label="Protein type">

        <a-select style="margin-left: 12px; width: 120px;" @change="handleProtChange"
          :value="initParamsData.selectProt">
          <a-select-option value="Cas9">dCas9</a-select-option>
          <a-select-option value="Cas12a">dCas12a</a-select-option>
          <a-select-option value="Cas12m">dCas12m</a-select-option>
          <a-select-option value="Others">Other</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="Gene number">
        <a-row>
          <a-col :span="12">
            <a-slider v-model:value="initParamsData.geneNumber" :min="1" :max="32" />
          </a-col>
          <a-col :span="4">
            <a-input-number v-model:value="initParamsData.geneNumber" :min="1" :max="32" style="margin-left: 16px" />
          </a-col>
        </a-row>
      </a-form-item>

      <a-form-item has-feedback :validate-status="validateValue('pam')"
        help="Should be combination of ATCGs, split by spaces" label="PAM Sequence">
        <a-input v-model:value="initParamsData.pamSequence" :disabled="disablePam" />
      </a-form-item>

      <a-form-item label="PAM position">
        <a-select :value="initParamsData.seqPosition" :disabled="disableSeqPosition">
          <a-select-option value="before">Before target</a-select-option>
          <a-select-option value="after">After target</a-select-option>
        </a-select>
      </a-form-item>

      <a-form-item label="Length of target">
        <a-input-number v-model:value="initParamsData.targetLength" :min="targetLengthMin" :max="targetLengthMax"
          :disabled="disableTargetLength" />
      </a-form-item>

      <a-form-item has-feedback :validate-status="validateValue('insulator')" help="Should be combination of ATCGs"
        label="Promoter">
        <a-textarea :showCount="false" v-model:value="initParamsData.insulatorSequence" />
      </a-form-item>

      <a-form-item has-feedback :validate-status="validateValue('spacer')" help="Should be combination of AGCUs"
        label="Repeat sequence">
        <a-textarea :showCount="false" v-model:value="initParamsData.spacerSequence" />
      </a-form-item>
      <!-- <a-form-item :wrapper-col="{ span: 14, offset: 4 }">
        <a-button type="primary" @click="onSubmit">Create</a-button>
        <a-button style="margin-left: 10px">Cancel</a-button>
      </a-form-item> -->
    </a-form>
  </div>
</template>

<script>

const labelCol = {
  style: {
    width: '150px',
  },
};
const wrapperCol = {
  span: 14,
};


export default {
  name: 'StepInitParams',
  setup() {
    return {
      labelCol,
      wrapperCol,
    }
  },
  emits: ['updateInitParamsData'],
  methods: {
    handleProtChange(value) {
      console.log('handleProtChange', value)
      // this.$emit('updateProt', value)
      this.initParamsData.selectProt = value
      this.initParamsData.pamSequence = this.protPamMap[value]

      if (value === 'Others') {
        this.disablePam = false
        this.disableSeqPosition = false
        this.disableTargetLength = false

        this.initParamsData.seqPosition = 'before'
        this.initParamsData.spacerSequence = ''
        this.initParamsData.insulatorSequence = ''

      } else {
        this.disablePam = true
        this.disableSeqPosition = true
        this.disableTargetLength = true

        this.initParamsData.seqPosition = 'before'

        switch (value) {
          case 'Cas12a':
            this.disableTargetLength = false
            this.initParamsData.targetLength = 23

            this.targetLengthMin = 23
            this.targetLengthMax = 25
            this.initParamsData.spacerSequence = 'TAATTTCTACTGTTGTAGAT'
            break

          case 'Cas9':
            this.initParamsData.targetLength = 20
            this.initParamsData.seqPosition = 'after'

            this.initParamsData.spacerSequence = 'GUUUGAGAGUUGUGUAAUUUAAGAUGGAUCUCAAAC'
            break

          case 'Cas12m':
            this.initParamsData.targetLength = 20
            this.initParamsData.spacerSequence = 'GUGUCAUAGCCCAGCUUGGCGGGCGAAGGCCAAGAC'
            break
        }
      }
    },

    validateValue(type) {
      switch (type) {
        case 'pam':
          for (let i = 0; i < this.initParamsData.pamSequence.length; i++) {
            let char = this.initParamsData.pamSequence.charAt(i)
            if (!['A', 'T', 'C', 'G', ' '].includes(char)) {
              return 'error'
            }
          }
          break

        case 'insulator':
          for (let i = 0; i < this.initParamsData.insulatorSequence.length; i++) {
            let char = this.initParamsData.insulatorSequence.charAt(i)
            if (!['A', 'T', 'C', 'G'].includes(char)) {
              return 'error'
            }
          }
          break

        case 'spacer':
          for (let i = 0; i < this.initParamsData.spacerSequence.length; i++) {
            let char = this.initParamsData.spacerSequence.charAt(i)
            if (!['A', 'T', 'C', 'G', 'U'].includes(char)) {
              return 'error'
            }
          }
          break
      }

      return 'success'
    },
  },
  data: () => ({
    protPamMap: {
      Cas9: 'AGG TGG CGG GGG',
      Cas12a: 'TTTA TTTG TTTC TTTT',
      Cas12m: 'TTC TTA',
      Others: '',
    },
    targetLengthMin: 1,
    targetLengthMax: 64,

    initParamsData: {
      selectProt: 'Cas9',
      geneNumber: 8,
      pamSequence: 'AGG TGG CGG GGG',
      targetLength: 20,
      seqPosition: 'before',
      insulatorSequence: 'AGCTGTCACCGGATGTGCTTTCCGGTCTGATGAGTCCGTGAGGACGAAACAGCCTCTACAAATAATTTTGTTTAA',
      spacerSequence: 'GUUUGAGAGUUGUGUAAUUUAAGAUGGAUCUCAAAC',
    },

    disablePam: false,
    disableTargetLength: false,
    disableSeqPosition: false,
  }),
  watch: {
    initParamsData: {
      deep: true,
      handler(newValue) {
        console.log('updateInitParamsData', newValue)
        this.$emit('updateInitParamsData', newValue)
      },
      immediate: true,
    }
  },
  beforeMount() {
    this.$emit('updateInitParamsData', this.initParamsData);

    this.initParamsData.selectProt = 'Cas9'
    this.initParamsData.pamSequence = this.protPamMap[this.initParamsData.selectProt]
    this.disablePam = true
    this.disableSeqPosition = true
    this.disableTargetLength = true

    this.handleProtChange('Cas9')
  },
  created() {
    this.$emit('updateInitParamsData', this.initParamsData);
  },
}
</script>
<style scoped></style>
