<template>
  <div>
    <a-steps :current="current" :items="items"></a-steps>
    <div class="steps-content">
      <div v-if="steps[current].content == 'init-params'">
        <!-- info-params -->
        <StepInitParams @updateInitParamsData="onUpdateInitParamsData" />
      </div>
      <div v-if="steps[current].content == 'gene-info'">
        <!-- gene-info -->
        <StepGeneInfo :initParamsData="initParams" @updateGeneInfoData="onUpdateGeneInfoData" />
      </div>
      <div v-if="steps[current].content == 'spacer-params'">
        <!-- gene-info -->
        <StepSpacerConfig :initParamsData="initParams" :spacer-config-input="spacerConfig"
          @update-spacer-config="i => spacerConfig = i" />
      </div>
      <div v-if="steps[current].content == '20nt-sequence'">
        <!-- 20nt-sequence -->
        <!-- <StepShow20nt :sequence20nt="geneInfo" /> -->
        <!-- <StepShow20nt :sequence20nt="geneInfo" :initParamsData="initParams" -->
        <!-- @update-sequence20nt="onUpdateSequence20nt" /> -->
        <ResultChart :spacerConfig="spacerConfig" />
      </div>
      <div v-if="steps[current].content == 'ga-params'">
        <StepGaParams @updateGaParamsData="onUpdateGaParamsData" />
      </div>
      <div v-if="steps[current].content == 'result'">
        <StepResult :init-params="initParams" :gene-info="geneInfo" :sequence20nt="sequence20nt"
          :ga-params="gaParams" />
      </div>
    </div>
    <div class="steps-action">
      <a-button v-if="current < steps.length - 1" type="primary" @click="next">Next</a-button>
      <!-- <a-button
        v-if="current == steps.length - 1"
        type="primary"
        @click="message.success('Processing complete!')"
      >
        Done
      </a-button> -->
      <a-button v-if="current > 0" style="margin-left: 8px" @click="prev">Previous</a-button>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { message } from 'ant-design-vue'

import StepInitParams from './StepInitParams.vue'
import StepGeneInfo from './StepGeneInfo.vue'
import ResultChart from './ResultChart.vue'
// import StepShow20nt from './StepShow20nt.vue'
import StepGaParams from './StepGaParams.vue'
import StepResult from './StepResult.vue'
import StepSpacerConfig from './StepSpacerConfig.vue'

const current = ref(0)
const next = () => {
  current.value++
}
const prev = () => {
  current.value--
}
const steps = [
  {
    title: 'Init Params',
    content: 'init-params',
  },
  {
    title: 'Gene Info',
    content: 'gene-info',
  },
  {
    title: 'Off-target Search Settings',
    content: 'spacer-params',
  },
  {
    title: 'Optional spacer',
    content: '20nt-sequence',
  },
  {
    title: 'GA Params',
    content: 'ga-params',
  },
  {
    title: 'Result',
    content: 'result',
  },
];
const items = steps.map(item => ({
  key: item.title,
  title: item.title,
}));

export default {
  name: 'MainView',
  setup() {
    return {
      current,
      steps,
      items,
      next,
      prev,
      message,
    }
  },
  components: {
    StepInitParams,
    StepGeneInfo,
    ResultChart,
    StepGaParams,
    StepResult,
    StepSpacerConfig,
  },
  methods: {
    onUpdateInitParamsData(data) {
      this.initParams = data

      setTimeout(() => {
        // eslint-disable-next-line no-undef
        pywebview && pywebview.api.setInitParam(
          data.geneNumber,
          data.pamSequence,
          data.insulatorSequence,
          data.spacerSequence,
          data.selectProt,
          data.targetLength,
          data.seqPosition,
        )
      }, 1000)
    },
    onUpdateGeneInfoData(data) {
      this.geneInfo = data

      // eslint-disable-next-line no-undef
      pywebview && pywebview.api.setGeneInfo(data)
    },
    onUpdateSequence20nt(data) {
      this.sequence20nt = data

      // eslint-disable-next-line no-undef
      pywebview && pywebview.api.updateSpacer(data)
    },
    onUpdateGaParamsData(data) {
      this.gaParams = data

      console.log(data)

      // eslint-disable-next-line no-undef
      pywebview && pywebview.api.setGaParam(
        data.population,
        data.termination,
        data.crossover,
        data.mutation,
        data.tournament,
      )
    },
  },
  data: () => ({
    initParams: {},
    geneInfo: [],
    gaParams: {},
    sequence20nt: [],
    spacerConfig: {
      species_gene: 'ecoli_mg1655_genome.fasta',
      match_pattern: 'NNNNNNNNNNNNNNNNNNNNNGG',
      DNA_bumps_num: 2,
      RNA_bumps_num: 2,
      mismatches: 3,
    }
  }),
}
</script>
<style scoped>
.steps-content {
  margin-top: 16px;
  border: 1px dashed #e9e9e9;
  border-radius: 6px;
  background-color: #fafafa;
  min-height: 200px;
  /* text-align: center; */
  padding-top: 16px;
}

.steps-action {
  margin-top: 24px;
}
</style>