  <a-spin :spinning="isLoading">
    <a-form layout="inline" :model="spacerFilterConfig" style="margin-bottom: 12px; margin-left: 12px;">
      <a-form-item label="Off Target">
        <a-select v-model:value="spacerFilterConfig.offtarget_threshold" style="width: 150px;"
          placeholder="Select a option">
          <a-select-option value="10">Top 10%</a-select-option>
          <a-select-option value="20">Top 20%</a-select-option>
          <a-select-option value="30">Top 30%</a-select-option>
          <a-select-option value="40">Top 40%</a-select-option>
          <a-select-option value="50">Top 50%</a-select-option>
        </a-select>
        <!-- <a-input v-model:value="filterForm.offTarget" placeholder="" /> -->
      </a-form-item>
      <a-form-item label="Efficiency">
        <a-select v-model:value="spacerFilterConfig.effiency_threshold" style="width: 150px"
          placeholder="Select a option">
          <a-select-option value="10">Last 10%</a-select-option>
          <a-select-option value="20">Last 20%</a-select-option>
          <a-select-option value="30">Last 30%</a-select-option>
          <a-select-option value="40">Last 40%</a-select-option>
          <a-select-option value="50">Last 50%</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="Energy">
