          <a-select-option value="30">Last 30%</a-select-option>
          <a-select-option value="40">Last 40%</a-select-option>
          <a-select-option value="50">Last 50%</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" @click="applyFilter">Filter</a-button>
      </a-form-item>
    </a-form>

    <s-table :columns="columns" :data-source="chartData" stripe :scroll="{ x: 2000 }" expand-row-by-click>
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'operation'">
          <a-button type="link" v-if="(record.CrisprScan_score !== '-' || record.Chari_score !== '-')"
            @click="removeRow(record.key)">Remove</a-button>
        </template>
      </template>
    </s-table>
  </a-spin>
