        effiency_threshold: '10',
        energy_threshold: '10',
      },
      isLoading: true,
      backendData: [],
      selectedRowKeys: [],
    }
  },
  computed: {
    chartData() {
      return this.backendData.map(item => {
        const line = {
          // key: item.Gene + Math.random(),
          key: item.key,
          origin: item,
          Gene: item.Gene,
          spacer: item.spacer,
          'free energy': item['free energy'],

          'target-sequence': '-',
          CropIT_score: '-',
          CCTop_score: '-',
          HsuSupp_score: '-',
          CFD_score: '-',

          // children: [],
          CrisprScan_score: this.formatNumber(item.efficiency.CrisprScan_score, 6),
          Chari_score: this.formatNumber(item.efficiency.Chari_score, 6),
          Housden_score: this.formatNumber(item.efficiency.Housden_score, 6),
        };


        if (item['off-target'].length === 1) {
          const i = item['off-target'][0];
          line['target-sequence'] = i['target-sequence']
          line['CropIT_score'] = this.formatNumber(i.CropIT_score, 6)
          line['CCTop_score'] = this.formatNumber(i.CCTop_score, 6)
          line['HsuSupp_score'] = this.formatNumber(i.HsuSupp_score, 6)
          line['CFD_score'] = this.formatNumber(i.CFD_score, 6)
        } else {
          line['children'] = item['off-target'].map((i) => {
            return {
              // key: item.Gene + i['target-sequence'] + Math.random(),
              key: i.key,
              // key: `${item.key}-${k}`,
              Gene: item.Gene,
              spacer: item.spacer,
              'free energy': item['free energy'],

              'target-sequence': i['target-sequence'],
              CropIT_score: this.formatNumber(i.CropIT_score, 6),
              CCTop_score: this.formatNumber(i.CCTop_score, 6),
              HsuSupp_score: this.formatNumber(i.HsuSupp_score, 6),
              CFD_score: this.formatNumber(i.CFD_score, 6),

              CrisprScan_score: '-',
              Chari_score: '-',
              Housden_score: '-',
            }
          })
        }

        return line;
      })
    },

    rowSelection: () => {
      return {
        selectedRowKeys: this.selectedRowKeys,
        onChange: this.onSelectChange,
      }
    }
  },
  beforeMount() {
    console.log('mounted', this.spacerConfig)
    this.loadData()
  },
  methods: {
    formatNumber(number, decimalPlaces) {
      if (typeof number !== 'number' || typeof decimalPlaces !== 'number' || decimalPlaces < 0) {
        number = parseFloat(number);
      }

      // 保留指定的小数点位数
      let formattedNumber = number.toFixed(decimalPlaces);

      // 去掉末尾的零
      formattedNumber = formattedNumber.replace(/\.?0+$/, '');

      return formattedNumber;
    },

    onSelectChange(changableRowKeys) {
      console.log('selectedRowKeys changed: ', changableRowKeys);
      this.selectedRowKeys.value = changableRowKeys;
    },

    removeRow(key) {
      console.log('removeRow', key)
      this.backendData = this.backendData.filter(item => {
        console.log('remove filter', item.key, key)
        return item.key !== key
      })
    },

    loadData() {
      this.isLoading = true
      this.selectedRowKeys.value = []
      // eslint-disable-next-line no-undef
      pywebview.api.getSpacer(this.spacerConfig, this.spacerFilterConfig).then((item) => {
        this.backendData = item.map((i, index) => {
          i.key = `${index}`;
          i['off-target'] = i['off-target'].map((i, k) => {
            i.key = `${index}-${k}`;
            return i;
          });
          return i;
        });
        console.log('filter', this.backendData, 'originData', item)
        this.isLoading = false

        // this.selectedRowKeys = (this.backendData.map(item => {
        //   return item['off-target'].map((i, k) => `${item.key}-${k}`)
        // })).flat()
      })
    },

    applyFilter() {
      this.loadData()
    }
  },
  watch: {
    backendData: {
      deep: true,
      handler() {
        console.log('backendData update', this.selectedRowKeys)
        // eslint-disable-next-line no-undef
        pywebview && pywebview.api.updateSpacer(this.backendData)
      }
    }
  },
