<script>

const labelCol = {
  style: {
    width: '200px',
  },
};
const wrapperCol = {
  span: 14,
};


export default {
  name: 'StepSpacerConfig',
  setup() {
    return {
      labelCol,
      wrapperCol,
    }
  },
  emits: ['updateSpacerConfig', 'updateSpacerFilterConfig'],
  methods: {
  },
  props: {
    spacerConfigInput: {
      type: Object,
      required: true,
    },
    initParamsData: {
      type: Object,
      required: true,
    }
    // spacerFilterConfig: {
    //   type: Object,
    //   required: true,
    // }
  },
  data: () => ({
    spacerConfig: {
      species_gene: 'ecoli_mg1655_genome.fasta',
      match_pattern: 'NNNNNNNNNNNNNNNNNNNNNGG',
      DNA_bumps_num: 2,
      RNA_bumps_num: 2,
      mismatches: 3,
    },
    // spacerFilterConfig: {
    //   offTarget: '50',
    //   efficiency: '50',
    //   freeEnergy: 50,
    // }
  }),
  mounted() {
    this.spacerConfig = this.spacerConfigInput;

    if (this.initParamsData && this.initParamsData.selectProt === 'Cas12a') {
      const num = this.initParamsData.targetLength;
      this.spacerConfig.match_pattern = 'TTTN' + 'N'.repeat(num);
    }

    if (this.initParamsData && this.initParamsData.selectProt === 'Cas12m') {
      // const num = this.initParamsData.targetLength;
      this.spacerConfig.match_pattern = 'TTC' + 'N'.repeat(20) + ',TTA' + 'N'.repeat(20);
    }
  },
  watch: {
    spacerConfig: {
      deep: true,
      handler(newValue) {
        this.$emit('updateSpacerConfig', newValue);
      },
      immediate: true,
    },
    // spacerFilterConfig: {
    //   deep: true,
    //   handler(newValue) {
    //     this.$emit('updateSpacerFilterConfig', newValue);
    //   },
    //   immediate: true,
    // }
  },
}
</script>
