<template>
  <div>
    <a-button style="margin-left: 24px;" :icon="h(ImportOutlined)" @click="openImport = true">Import</a-button>

    <a-modal v-model:open="openImport" title="Import data" @ok="handleOk">
      <a-textarea v-model:value="importValue"
        placeholder="CSV: name, sequence, Binds to coding strand(0/1), Binds to non-coding strand(0/1)" :rows="5" />
    </a-modal>

    <a-form :model="geneInfoData" :label-col="labelCol" :wrapper-col="wrapperCol" layout="horizontal">
      <div v-for="item of geneInfoData" :key="item.geneName">
        <a-form-item label="Gene name" has-feedback :validate-status="validateGeneName(item.geneName)"
          help="Gene name should be unique">
          <a-input v-model:value="item.geneName" />
        </a-form-item>
        <a-form-item label="Gene sequence" has-feedback :validate-status="validateGeneSeq(item.geneSequence)"
          help="Gene sequence should not be empty">
          <a-textarea v-model:value="item.geneSequence" />
        </a-form-item>
        <a-form-item label="Option">
          <a-checkbox-group v-model:value="item.option" :options="options">
            <!-- <a-checkbox value="isPositive" name="type">Negative Sequence</a-checkbox>
            <a-checkbox value="findNegative20nt" name="type">Find 20nt on negative</a-checkbox> -->
          </a-checkbox-group>
        </a-form-item>
      </div>
    </a-form>
  </div>
</template>

<script>

const labelCol = {
  style: {
    width: '250px',
  },
}

const wrapperCol = {
  span: 14,
}

import { h } from 'vue'
import { ImportOutlined } from '@ant-design/icons-vue'

export default {
  name: 'StepGeneInfo',
  setup() {
    return {
      labelCol,
      wrapperCol,
      h,
      ImportOutlined,
    }
  },
  props: {
    initParamsData: {
      type: Object,
      required: true,
    },
  },
  emits: ['updateGeneInfoData'],
  methods: {
    handleOk() {
      this.openImport = false

      const csv = this.importValue.split('\n')
      // csv.shift()

      for (const index in csv) {
        const item = csv[index].split('\t').map(item => item.trim())
        if (item.length !== 4) {
          continue
        }

        this.geneInfoData[index] = {
          geneName: item[0],
          geneSequence: item[1],
          option: [],
        }

        this.geneInfoData[index].option.push(item[2] === '1' ? 'coding' : '')
        this.geneInfoData[index].option.push(item[3] === '1' ? 'non-coding' : '')
      }
    },
    validateGeneName(value) {
      // check duplicate gene name

      const geneNames = this.geneInfoData.map(item => item.geneName)
      if (geneNames.filter(item => item === value).length > 1) {
        return 'error'
      }

      if (value === '') {
        return 'error'
      }
      return 'success'
    },

    validateGeneSeq(value) {
      if (value === '') {
        return 'error'
      }
      return 'success'
    },
  },
  data: () => ({
    options: [
      {
        label: 'Binds to coding strand',
        value: 'coding',
        disabled: false,
      },
      {
        label: 'Binds to non-coding strand',
        value: 'non-coding',
        disabled: false,
      },
    ],
    geneInfoData: [],
    openImport: false,
    importValue: '',
    defaultValue: [
      ['', 'gpmA'],
      ['', 'fbaA'],
      ['', 'pykF'],
      ['', 'pfkB'],
      ['', 'pykA'],
      ['', 'gpmM'],
      ['', 'pfkA'],
      ['', 'fbaB'],
    ],
  }),
  beforeMount() {
    let selOption = []
    switch (this.initParamsData.selectProt) {
      case 'Cas9':
        this.options[0].disabled = true
        this.options[1].disabled = false
        selOption = ['coding', 'non-coding']
        break

      case 'Cas12m':
      case 'Cpf1':
      case 'Cas12a':
        this.options[0].disabled = false
        this.options[1].disabled = false
        selOption = ['coding']
        break
    }
    console.log(this.initParamsData.selectProt, selOption, this.options)

    this.geneInfoData = []
    for (let i = 0; i < this.initParamsData.geneNumber; i++) {
      this.geneInfoData.push({
        geneName: this.defaultValue[i] ? this.defaultValue[i][1] : `Gene ${i + 1}`,
        geneSequence: this.defaultValue[i] ? this.defaultValue[i][0] : '',
        option: this.defaultValue[i] ? Array.from(['coding', 'non-coding']) : [],
      })
    }
  },
  watch: {
    geneInfoData: {
      deep: true,
      handler(newValue) {
        this.$emit('updateGeneInfoData', newValue);
      }
    }
  },
}
</script>
<style scoped></style>
