<template>
  <div>
    <a-form :model="gaParamsData" :label-col="labelCol" :wrapper-col="wrapperCol">
      <a-form-item label="Population size">
        <a-row>
          <a-col :span="12">
            <a-slider v-model:value="gaParamsData.population" :min="10" :max="10000" />
          </a-col>
          <a-col :span="4">
            <a-input-number v-model:value="gaParamsData.population" :min="10" :max="10000" style="margin-left: 16px" />
          </a-col>
        </a-row>

      </a-form-item>
      <a-form-item label="Termination generation">
        <a-row>
          <a-col :span="12">
            <a-slider v-model:value="gaParamsData.termination" :min="10" :max="1000" />
          </a-col>
          <a-col :span="4">
            <a-input-number v-model:value="gaParamsData.termination" :min="10" :max="1000" style="margin-left: 16px" />
          </a-col>
        </a-row>
      </a-form-item>
      <a-form-item label="Crossover probability">
        <a-input-number v-model:value="gaParamsData.crossover" :min="0" :max="0.99" :step="0.01"
          style="margin-left: 16px" />
      </a-form-item>
      <a-form-item label="Mutation probability">
        <a-input-number v-model:value="gaParamsData.mutation" :min="0" :max="1" :step="0.0001"
          style="margin-left: 16px" />
      </a-form-item>
      <a-form-item label="Tournament size">
        <a-row>
          <a-col :span="12">
            <a-slider v-model:value="gaParamsData.tournament" :min="2" :max="gaParamsData.population" />
          </a-col>
          <a-col :span="4">
            <a-input-number v-model:value="gaParamsData.tournament" :min="2" :max="gaParamsData.population"
              style="margin-left: 16px" />
          </a-col>
        </a-row>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>

const labelCol = {
  style: {
    width: '200px',
  },
};
const wrapperCol = {
  span: 14,
};


export default {
  name: 'StepGaParams',
  setup() {
    return {
      labelCol,
      wrapperCol,
    }
  },
  emits: ['updateGaParamsData'],
  methods: {
  },
  data: () => ({
    gaParamsData: {
      population: 1000,
      termination: 30,
      crossover: 0.7,
      mutation: 0.1,
      tournament: 4,
    },
  }),
  watch: {
    gaParamsData: {
      deep: true,
      handler(newValue) {
        console.log('updateGaParamsData', newValue)
        this.$emit('updateGaParamsData', newValue);
      },
      immediate: true,
    }
  },
}
</script>
