  computed: {
    legend: function () {
      const legend = new Set()
      legend.add('A')
      legend.add('T')
      legend.add('C')
      legend.add('G')

      const d = this.resultList[0].geneList
      for (const i of d) {
        legend.add(i.type)
      }
      return legend
    },
    analysisResultComputed() {
      const obj = {}
      for (const key in this.analysisResult) {
        const data = this.analysisResult[key]
        obj[key] = {
          ka_decrease: genStyle(data['ka_decrease']),
          kclv_decrease: genStyle(data['kclv_decrease']),
          RNA_DNA_eng_weighted: genStyle(data['RNA_DNA_eng_weighted']),
          DNA_DNA_opening: genStyle(data['DNA_DNA_opening']),
          spacer_self_fold: genStyle(data['spacer_self_fold']),
        }
      }

      return obj;
    }
  },
  watch: {
    openExport(newVal) {
      if (newVal) {
        this.exportCsvData = 'FreeEng,Sequence\n'

        for (const item of this.resultList) {
          this.exportCsvData += `${item.freeEnergy}, `
          for (const gene of item.geneList) {
            this.exportCsvData += `${gene.type}: ${gene.sequence}| `
          }
          this.exportCsvData += '\n'
        }
      }
    },

    status(newVal) {
      if (newVal === 'done') {
        setTimeout(() => {
          // this.showResult()
          this.resultEnergy()
        }, 1000)
      } else {
        if (this.root) {
          this.root.dispose()
        }
        if (this.root1) {
          this.root1.dispose()
        }
      }

      if (newVal !== this.gaParams.termination && this.status === 'done') {
        setTimeout(() => {
          // eslint-disable-next-line no-undef
          pywebview.api.getResult().then((item) => {
            this._resultList = item
            this.resultList = item.slice(0, parseInt(this.selectNum))
            console.log(item)

            setTimeout(() => {
              this.status = 'done'
            }, 100)
          })

          this.handleExportCopy()
        }, 100)
      }
    },

    selectNum() {
      setTimeout(() => {
        // this.showResult()
        this.resultList = this._resultList.slice(0, parseInt(this.selectNum))
        this.resultEnergy()
      }, 100)
    },

    nowIter(newVal) {
      if (newVal === this.gaParams.termination || this.status === 'done') {
        setTimeout(() => {
          // eslint-disable-next-line no-undef
          pywebview.api.getResult().then((item) => {
            this._resultList = item
            this.resultList = item.slice(0, parseInt(this.selectNum))
            console.log(item)

            setTimeout(() => {
              this.status = 'done'
            }, 100)
          })

          if (this.status !== 'done')
            this.handleExportCopy()
        }, 200)
      }
    }
  },
}
